  /* ----- CSS Animations --------- */
  /* Scroll Down Arrow */
  .section {
    position: relative;
    cursor: pointer;
    z-index: 2;
    left: -3%;
  }
  .section::before {
    animation: bounce 1.5s ease infinite;
    bottom: 164vh;
    color: #fff;
    content: '╲╱';
    font-size: 3vh;
    letter-spacing: -1px;
    line-height: 7vh;
    filter:drop-shadow(0px 5px 6px black);
    position: absolute;
    text-align: center;
    border-radius: 10px;
    padding: 0rem .5rem;
    background-color: rgb(0, 0, 0, 0.1);
  }
  @keyframes bounce {
    50% {
      transform: translateY(-80%);
    }
  }

  /* Fade on load */
  .mountains, .sun, .pg, .rocket, .html, .css, .java, .javascript, .typescript, .node, .postgresql, .python, .git, .mui, .jQ, .redux, .react, .saga, .sql, .spring  {
    animation: fade 5s;
  }
  @keyframes fade {
    0% {opacity: 0;}
    100% {opacity: 1;}
  }

  /* Fade + Scale on load */
  .username {
    animation: scaleUp 5s;
  }
  @keyframes scaleUp {
    0% {opacity:0; transform: scale(.2);}
    100% {opacity:1; transform: scale(1);}
  }
  
