.cloud-content {
    display:inline-flex;
    align-items: center;
    justify-content:center;
    padding-top:-10px;
    padding-bottom: 0px;
}

.cloud {
    display:inline-block;
    background:white;
    width:120px;
    height:120px; 
    border-radius:50%;
    position:relative;
    top:-120px;
    -webkit-filter: blur(6px);
    z-index:0;
    left:-50px;
    animation: 25000ms cloudAnimation linear infinite;
    opacity:0.76;
}

.cloud:before {
    content:"";
    display:inline-block;
    background:white;
    width:100px;
    height:100px; 
    -webkit-filter: blur(3px);
    position:relative;
    border-radius:50%;
    top:-30px;
    left:60px;
}
.cloud:after {
    content:"";
    display:inline-block;
    background:white;
    width:200px;
    height:100px; 
    -webkit-filter: blur(3px);
    position:relative;
    border-radius:50%;
    top:-80px;
    left:70px;
}

.cloud:first-child {zoom:1.5;}

@keyframes cloudAnimation {
    0%{
        transform: translate(-600px,0);
    }
    100% {
        transform: translate(800px,0);
    }
}